body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  overflow: hidden;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
}

.navbar {
  padding-left: 3%;
  padding-right: 2%;
  padding-bottom: 1%;
  padding-top: 2vh;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  z-index: 7;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
}

.logo {
  margin-left: 2%;
  width: 47vw;
  max-width: 470px;
}

.menuBar {
  display: flex;
  justify-content: space-between;
  width: 45vw;
  max-width: 700px;
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-bottom-width: 1.5px;
  border-color: rgba(255, 255, 255, 0.7);
  border-style: solid;
  white-space: nowrap;
  padding-left: 1%;
  padding-right: 1%;
}
#link {
  margin-bottom: 5px;
  font-family: 'Times New Roman', Times, serif;
  font-size: 20px;
  color: rgb(255, 255, 255);
  background-color: rgba(0, 0, 0, 0.2);
  padding-left: 2.5%;
  padding-right: 2.5%;
  padding-top: 1.5%;
  padding-bottom: 1.5%;
  border-radius: 5%;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}
#link:hover {
  -webkit-transform: scale(1.10);
          transform: scale(1.10);
  color: rgb(235, 235, 235);
}


body {
  overflow: hidden;
}

video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%; 
  min-height: 100%;
}

@media only screen and (max-width: 1400px) {
  .video {
    overflow: hidden;
    position: absolute;
    right: 0;
    margin-top: 35vh;
    max-width: 100%; 
    height: auto;
    z-index: 1;
  }
}

.error {
  display: flex;
  align-content: center;
  justify-content: center;
}
body {
  overflow: auto;
}

.header {
  width: 100%;
  text-align: center;
  margin-top: 6%;
  border-top: 00;
  border-left: 0;
  border-right: 0;
  border-style: solid;
  border-color: rgba(255, 255, 255, 0.8);
  border-bottom-width: 1px;
}
.header h1 {
  font-family: "TimesNewRoman";
  padding-bottom: 2%;
  font-weight: 200;
  word-spacing: 8%;
